<template>
  <button
    :class="[
      'c-button', `theme-green`,
      {'hover-text': withHoverText}
    ]"
    :data-hover-text="hoverText"
  >
    <span class="btn-text">
      <slot></slot>
    </span>
  </button>
</template>

<script>
export default {
  name: 'XButton',
  props: {
    hoverText: {
      type: String,
      default: 'default hoverText'
    }
  },
  computed: {
    withHoverText () {
      return this.hoverText?.length
    }
  }
}
</script>

<style src="./xButton.scss" scoped lang="scss"></style>
